.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 60px 34px 27px 34px;

  &__text {
    text-align: center;
    color: var(--txt-color-grey);
  }
}

.heart {
  color: red;
}

@media screen and (min-width: 768px) {
  .footer {
    flex-direction: row;
    margin: auto;
    padding-bottom: 18px;
  }
}

@media screen and (min-width: 1280px) {
  .footer {
    flex-direction: row;
    margin-right: auto;
    margin-left: auto;
    padding-top: 80px;
    padding-bottom: 19px;
  }
}
