.select-menu {
  max-width: 270px;
}

.select-menu .select-btn {
  height: 40px;
  width: 270px;
  color: var(--txt-color-grey);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  cursor: pointer;
  background: rgba(255, 255, 255, .2);
  border-style: none;
  border-radius: 20px;
  align-items: center;
  padding-left: 30px;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  display: flex;
}

.select-menu .options {
  width: 270px;
  max-height: 295px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  margin-top: 10px;
  padding: 1px;
  animation-name: fadeInDown;
  animation-duration: .35s;
  animation-fill-mode: both;
  display: none;
  position: absolute;
  overflow-y: auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.select-menu .options .option {
  height: 30px;
  cursor: pointer;
  background: rgba(255, 255, 255, .2);
  align-items: center;
  padding: 0 16px;
  display: flex;
}

.select-menu .options .option:hover {
  background: #86397a;
}

.select-menu .options .option i {
  margin-right: 12px;
  font-size: 25px;
}

.select-menu .options .option .option-text {
  color: var(--txt-color-grey);
  font-size: 16px;
  font-weight: 400;
}

.select-menu.active .options {
  opacity: 0;
  z-index: 3;
  animation-name: fadeInUp;
  animation-duration: .4s;
  animation-fill-mode: both;
  display: block;
}

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 30px, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  to {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
}

.select-menu .options::-webkit-scrollbar {
  width: 10px;
}

.select-menu .options::-webkit-scrollbar-track {
  background: #4e2248;
  border-radius: 20px;
}

.select-menu .options::-webkit-scrollbar-thumb {
  background-color: #b838a4;
  border: 1px solid #000;
  border-radius: 20px;
}

.option span {
  pointer-events: none;
}

.spinner {
  margin-right: -50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading {
  justify-content: center;
  display: flex;
}

.loading--full-height {
  height: 100%;
  align-items: center;
}

.loading:after {
  content: "";
  width: 100px;
  height: 100px;
  border: 10px solid #dc56c5;
  border-color: #dc56c5 rgba(221, 221, 221, .82);
  border-radius: 50%;
  animation: 1s infinite loading;
}

@keyframes loading {
  to {
    transform: rotate(1turn);
  }
}

